.cardContainer {
    border: 2px solid red;
    width: 100%; 
    margin: auto;
    margin-top: 100px;
    perspective: 1000px;
    padding: 20px 100px;
    
}

.singleContainer {
    margin-top: 40px; 
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem 4rem;
    /* border: 2px solid blueviolet; */
    width: 400px;
}

.card {
    transform-style: preserve-3d;
    width: 500px;
    padding: 2rem 4rem;
    border-radius: 30px;
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2);
}

.imga {
    width: 300px;
    height: 200px;
    z-index: 23;
    transition: all 0.75 ease-in-out;
}

.sneaker {
    margin-bottom: 30px;
    position: relative;
}

.description h2 {
    margin-bottom: 20px;
    font-size: 40px;
    font-family: 'Poppins', sans-serif;
    transition: all 0.75 ease-in-out;
}

.description h3 {
    font-size: 20px;
    margin-bottom: 30px;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    transition: all 0.75 ease-in-out;
}

.size {
    display: flex;
    justify-content: space-around;
}

