.purchase {
    width: 100%;
    margin-top: 12px;
    padding: 10px 20px;
    border: 0;
    background-color: #f54642;
    border-radius: 30px;
    color: #fff;
    cursor: pointer;
}

.card h4 {
    font-size: 19px;
}

.less {
    width: 100%;
    margin-top: 12px;
    padding: 10px 20px;
    border: 0;
    background-color: #f54642;
    border-radius: 30px;
    color: #fff;
    cursor: pointer;
}

@media only screen and (max-width: 600px) {

    .singleContainer {
        margin-left: 0;
    }

    .home { 
        /* border: 2px solid red;  */
        grid-template-columns: 1fr;
        margin: auto;
        margin-top: 40px;
        margin-bottom: 40px;
    }

