* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.datas {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100%; 
  margin-top: 100px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  padding: 20px 80px;
}

@media only screen and (max-width: 1500px) {

  .datas {
    grid-template-columns: 1fr 1fr;
  }
  

}

@media only screen and (max-width: 1084px) {

  .datas {
    grid-template-columns: 1fr;
  }
  

}

@media only screen and (max-width: 524px) {

  .datas {
    margin: 0;
    padding: 0;
    margin-top: 20px;
  }

}
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 40px;
    border-bottom: 1px solid #b0b0b3;
    height: 80px;
}

.mainnav {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 50%;
    padding: 20px 30px;
    list-style: none;
}


.mainnav li a {
    
    color: #787783;
    font-family: 'Georama', sans-serif;
    font-weight: bolder;
    text-decoration: none;
    font-size: 18px;
    
}


.signup button {
    padding: 15px 30px;
    background-color: #ea4c89;
    color: #fff;
    border-radius: 9px;
    border: 0;
    font-family: 'Georama', sans-serif;
}

.lines {
    display: none;
}

.line {
    width: 40px;
    height: 5px;
    margin-bottom: 2px;
    background-color: #ea4c89;
}

.responsive {
    color: #000;
    font-family: 'Style Script';
    font-weight: bolder;
    font-size: 30px;
    display: none;
}

@media only screen and (max-width: 1128px) {

    .navbar {
        justify-content: space-between;
    }

    .none {
        display: none;
    }

    .lines {
        margin-left: 30px;
        display: inline;
        display: initial;
        cursor: pointer;
    }
    .responsive {
        display: inline;
        display: initial;
    }
    

  }

  @media only screen and (max-width: 444px) {

    
    .responsive {
        font-size: 20px;
    }

    .signup button {
        padding: 5px 10px;
    }

    .line {
        width: 20px;
        height: 3px;
        margin-bottom: 2px;
    }

    .mainnav {
        flex-direction: column;
        align-items: flex-start;
    }

  }

