.home{
    height: 600px;
    background-color: thistle;
    display: flex;
    align-items: center;
}
.home2{
    height: 100px;
    background-color: white;
}
.home3{
    margin-top: 10px;
    font-size: large;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    height: 600px;
    background-color: rgb(226, 210, 226);
    background-image: linear-gradient(to right , #FF8008,#FFC837);
    display: flex;
    flex-direction: row;
    align-items: center;
    color: rgb(255, 255, 255);
}
h1{
    padding: 20px;
}
.img1{
    border-radius: 40px;
    height: 70%;
